@import "variables.scss";

* {box-sizing: border-box;
    padding: 0;
    margin: 0;
    outline: none;
    border: none;
    background-color: inherit;
    text-transform: none;
    text-decoration: none;
    color: inherit;
}

html, body {
max-height: 100vh;
height: 100%;
}

@media all and (orientation:landscape) {
    html, body {
        max-height: none;
        height: 100%;
        }
}
    

body {
    display: flex;
    flex-direction: column; 
    justify-content: space-between;
}

main {
    display: flex;
    flex: 1;
    flex-direction: column; 
    overflow: hidden;          
}

a, span, p, input, button, date, select, option, textarea, ::placeholder {
    font-family: 'Oswald', sans-serif;
    font-size: clamp(0.9rem, 1.6vw, 1.2rem);
    line-height: 1.2;
    color:$font-color-board;
}

select {
    padding: clamp(0.3rem, 0.7vw, 0.5rem); 
    border: #7c24af;
    outline-color: #7c24af;
    border-radius: 0.2rem;
    padding-right: 2em;
    appearance: none;
    background-repeat: no-repeat;
    background-size: 20% 60%;
    background-position: calc(100% + 0.2em) 0.5em;    
    background-image: url('../image/arrow.svg');
}

select:hover {
    background-image: url('../image/arrow_hover.svg');
}

.wrapper{    
    display: flex;    
    justify-content: center;
    align-items: flex-start; 
    background-color: #575753; 
    min-height: 100%;
    min-width: 15rem;
    overflow: auto;
    
  }

.container {
    max-width: 1416px;     
    flex: 1;    
    padding: 0.5rem;
    height: 100%;     
    overflow: hidden;
}

.header {   
    display: flex;
    justify-content: space-between; 
    align-items: center;
    border: 1px solid #fff;
    border-radius: 1rem;
    padding-top: clamp(2rem, 5vw, 4rem);
    padding-bottom: clamp(2rem, 5vw, 4rem);
    padding-left: clamp(1rem, 2.5vw, 2rem);
    padding-right: clamp(1rem, 2.5vw, 2rem);    
    background: linear-gradient(90deg, #d44d34,#af31ab,#6cd5c0); 
  
    &__text {
        text-transform: uppercase; 
        color: #fff; 
        font-size: clamp(1.5rem, 3vw, 2.5rem);    
    }

    &__time {
        border: 1px solid #000;
        border-radius: 0.5rem;  
        padding: clamp(0.5rem, 1.25vw, 1rem); 
        background-color: rgb(231, 221, 221); 
        color: rgb(30, 17, 146); 
        font-size: clamp(1rem, 2vw, 1.5rem);
    }
}

.btn {
    border: 1px solid rgb(241, 234, 234);
    padding: clamp(0.25rem, 0.5vw, 0.5rem);
    border-radius: 0.3rem; 
    color:rgb(241, 233, 233);  
    background-color: #a65dcf;                                  
}

.btn:hover {                    
    background-color: #763999; 
    border: 1px solid #673485;                                                    
}

.btn:active {
    border: 1px solid rgb(241, 234, 234);  
}          

date {
display: block;
text-align: end;
}

.board-wrapper {    
    display: flex;
    justify-content: space-between; 
    max-height: 100%;    
    gap:1rem;  
    margin-left: auto;
    margin-right: auto; 

    .board {              
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 1;        
        min-height: 100%;                   
        border: 1px solid #fff;
        border-radius: 0.5rem;        
               
        &__header {
            position: relative;  
            display: flex;
            justify-content: space-between; 
            align-items: center;
            border-bottom: 1px solid #fff;
            border-radius: 0.5rem;           
            padding: clamp(0.75rem, 2vw, 1.5rem);
            text-transform: uppercase;            

            p, span {
                font-size: clamp(1rem, 2.2vw, 1.5rem);
            }


        }

        .todo, .in-progress, .done {            
            color:$font-color-board;
        }
   
       .todo {        
           background: $background-color-todo;             
        }
       
       .in-progress {        
           background: $background-color-inprogress;
       }
   
       .done{        
           background: $background-color-done;
       }

       .in-progress-wrapper {
           border-radius: 0.5rem;
       }

       &__task-wrapper {
           position: relative;
            min-height: clamp(3rem, 6vw, 5rem);
            height: 100%;
            flex:1;            
            padding: clamp(0.25rem, 0.5vw, 0.5rem);
            overflow: auto;

            .empty-message {               
                text-align: center;
            }
                      
            .task_to-do {
                background: $background-color-todo;
            }

            .task__title {
               font-weight: 700;               
            }

            .task__title, .task__description, .task__user {
                margin-right: clamp(0.25rem, 5vw, 1.8rem);
                margin-top: clamp(0.4rem, 0.8vw, 0.8rem); 
                overflow: hidden; 
                text-overflow: ellipsis;
            }

            .task__description, .task__user, .task__date {
                font-size: clamp(0.8rem, 1.5vw, 1.2rem);                             
            }

            .task__user {
                font-style: italic;
            }
            

            .task {
                position: relative;
                border: 1px solid #fff;
                border-radius: 0.5rem; 
                padding: clamp(0.4rem, 0.8vw, 0.8rem);                
                margin-top: clamp(0.25rem, 0.5vw, 0.5rem);
                margin-bottom: clamp(0.25rem, 0.5vw, 0.5rem);

                .to-progress {
                    position: absolute;
                    right: clamp(0.4rem, 0.83vw, 0.625rem); 
                    top: 50%;  
                    width: 20px;
                    height: clamp(2rem, 5vw, 3.75rem);                   
                    transform: translateY(-20%);       
                }

                .to-progress::before {
                    content: '';
                    width: 2px;
                    height: 50%;
                    background-color: #42423f;
                    position: absolute;                    
                    transform: rotateZ(-45deg) ;                  
                    right:0.7rem;
                    top: 0px;                    
                }

                .to-progress::after {
                    content: '';
                    width: 2px;
                    height: 50%;
                    background-color: #42423f;
                    position: absolute;                    
                    transform: rotateZ(-135deg) translate(-50%);                 
                    right:0.7rem;
                    top: 32%; 
                }

                .to-progress:hover::after{
                    background-color: #c4a2d8;                  
                    width: 3px;
                }

                .to-progress:hover::before{
                    background-color: #c4a2d8;                  
                    width: 3px;
                }

                .to-progress:active::after{
                    background-color: #fff;                    
                }

                .to-progress:active::before{
                    background-color: #fff;
                }

                .task__btn {
                    display: flex;
                    justify-content: end;
                    gap: clamp(0.5rem, 1vw, 1rem);                    
                }               
            }
        }

              &__footer { 
            border-top: 1px solid #fff;
            border-radius: 0.5rem;               
            padding: 0;                            
        }

        &__footer-btn {        
            padding: clamp(0.75rem, 2vw, 1.5rem);        
            width: 100%;
            border-color: inherit;
            border-radius: 0.5rem;   
            background-color: #a65dcf;
            color: $font-color-board;            
        }

        &__footer-btn:hover { 
            background-color: #9714b1;  
        }

        &__footer-btn:active {         
            color: #111414; 
        }
    } 
}


@media (max-width:676px) {

    @media screen and (orientation: landscape) {
        .board-wrapper {    
            align-items: flex-start;   
        }
    
        .board{
            height: 100%;
            align-items:stretch;            
            width: 100%;    
        }
    
    }
       .board-wrapper {    
        flex-direction: column; 
        max-width: 25rem;
        max-height: 100%;  
        overflow: hidden;

        .board {       
           align-items: stretch; 
           max-height: 50%;       
           height: 100%;
           
            .board__header {                    
            padding-right: 3rem;                                     
            }

            .board__header::after {                    
            content: url('../image/arrow.svg');  
            height: 1.5rem;
            width: 1.2rem;   
            position: absolute;
            right: 0.5rem; 
            top: 50%;
            transform: translateY(-60%) rotate(180deg);   
            }

           .board__task-wrapper{ 
            opacity: 1;                    
            transition: all 0.2s ease-in-out;
            }   

            .board__footer{ 
                opacity: 1;      
                transition: all 0.2s ease-in-out;
            }
        }

            .task-hidden {    
                 min-height: auto;
                 border: none;                 
                 
                 .board__header {                    
                     border-bottom: 2px solid #fff;                                       
                 }

                 .board__header::after {                    
                   transform: translateY(-40%) rotate(0deg); 
                }

                .board__header::after:active {                    
                    content: url('../image/arrow_hover.svg');
                }
                     
                .board__task-wrapper{ 
                    max-height: 0px;  
                    min-height: auto;                 
                    opacity: 0%;    
                    transition: all 0.2s ease-in-out;
                }   
                              
                .board__footer{ 
                    max-height: 0px;                                       
                    opacity: 0%;  
                    transition: all 0.2s ease-in-out;
                }
            }        
    }   
}

@media (max-width:767px) {
    @media screen and (orientation: landscape) {
        .header {   
            padding-top: clamp(1rem, 2.5vw, 2rem);
            padding-bottom: clamp(1rem, 2.5vw, 2rem);                 
        }

        .board-wrapper {    
          max-height:unset; 
          .board__task-wrapper {
            min-height: 0px; 
          }          
        }
}}

.overlay, .overlay-warning {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    display: none;
    background-color: rgba(0, 0, 0, 0.65);/* фон затемнения */
    position: fixed; /* фиксированное поцизионирование */
}
    .task__edit{
    position: fixed;
    top: -100%;    
    left: 50%;
    min-width: clamp(20rem, 40vw, 30rem);    
    transform: translate(-50%, -500%);    
    z-index: 20;
    width: clamp(12rem, 30vw, 25rem);
    padding: clamp(0.5rem, 1.3vw, 1rem);
    border: 1px solid #fff;
    border-radius: 0.5rem; 
    background:$background-color-todo;    
    transition: transform 0.6s ease-out;
    
    p, a, input, button, select, ::placeholder {
        color:rgb(232, 233, 229);
    }

    .task__edit-title {
        width: 100%;
        border: 1px solid #7c24af;
        border-radius: 0.5rem;
        padding: clamp(0.4rem, 0.8vw, 0.8rem);
    }
   
    .task__edit-description {
        max-width: 100%;
        min-width: 100%;
        min-height: 8rem;
        max-height: 12rem;
        margin-top: clamp(0.25rem, 0.5vw, 0.5rem);
        margin-bottom: clamp(0.25rem, 0.5vw, 0.5rem);
        padding: clamp(0.25rem, 0.5vw, 0.5rem);
        border: 1px solid #7c24af;
        border-radius: 0.5rem;         
    }

    .task__edit-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap:1rem;
        
        .empty {
            flex: 1;
        }
    }
}

select .user {
    background-color:#ce3e24;  
}

.warning{
    position: fixed;
    top: -100%;    
    left: 50%;   
    transform: translate(-50%, -500%);    
    z-index: 20;
    width: clamp(15rem, 27vw, 20rem);
    padding: clamp(1rem, 2.7vw, 2rem);
    border: 1px solid #7c24af;
    border-radius: 0.5rem; 
    background:$background-color-todo;    
    transition: transform 0.6s ease-out;

    p {
        text-align: center;
    }
    .warning__text {
        margin-top: clamp(1rem, 2.7vw, 2rem);
        margin-bottom: clamp(0.5rem, 1.4vw, 1rem);
        
    }    

    .warning__buttons {
        margin-top: clamp(1rem, 2.7vw, 2rem);
        display: flex;
        justify-content: center;
        align-items: center;
        gap:clamp(1rem, 2.7vw, 2rem);
    }
}

.overlay-active {
    display: block;
}

.overlay-active+.task__edit {   
    transform: translate(-50%, -50%);
    top: 50%;
}

.warning-active {
    display: block;
}

.warning-active+.warning {   
    transform: translate(-50%, -50%);
    top: 50%;
}

.task_in-progress {
    background: $background-color-inprogress;
}
    
.task_done {
    background: $background-color-done;
} 

.board__task-wrapper::-webkit-scrollbar, select::-webkit-scrollbar {
    width: clamp(1.2rem, 2vw, 1.5rem); /* ширина скролла */    
    background-color: #af3176;
}

/* ползунок скроллбара */
.board__task-wrapper::-webkit-scrollbar-thumb, select::-webkit-scrollbar-thumb {
    background-color: #7c24af;
    border-radius: 9em;
    box-shadow: inset 1px 1px 10px #faf3f5;
}

.board__task-wrapper::-webkit-scrollbar-thumb:hover, select::-webkit-scrollbar-thumb:hover {
    background-color: #841f9b;
    box-shadow: inset 1px 1px 5px #faf3f5;
}


